import React, { Component } from "react";
import "./personalInfo.scss";
import MessageBlock from "@/components/common/messageBlock";
import { messageBlocks } from "@/datebase/personalInfo.json";

class PersonalInfo extends Component {
  state = {
    messageBlocks: [],
  };

  componentDidMount() {
    this.setState({ messageBlocks });
  }

  render() {
    const { messageBlocks } = this.state;

    return (
      <React.Fragment>
        <div id="personalInfo">
          <div id="piTitle">
            <p>个人信息</p>
          </div>
          <div id="messageBlocks">
            {messageBlocks.map((messageBlock) => (
              <MessageBlock
                key={JSON.stringify(messageBlock)}
                mbTitle={messageBlock.mbTitle}
                mbContents={messageBlock.mbContents}
              ></MessageBlock>
            ))}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default PersonalInfo;
