import React, { Component } from "react";
import "./homePage.scss";
import { title, content1, content2 } from "../datebase/homePage.json";

class HomePage extends Component {
  render() {
    return (
      <div id="homePage">
        <h1>{title}</h1>
        <p>{content1}</p>
        <p>{content2}</p>
      </div>
    );
  }
}

export default HomePage;
