import React, { Component } from "react";
import "@/components/common/webBlock.scss";
import "font-awesome/css/font-awesome.css";

class WebBlock extends Component {
  state = {
    showWebDetail: false,
    showingImgIndex: 0,
    picturesNumber: 0,
  };

  componentDidMount() {
    this.setState({ picturesNumber: this.props.webImages.length });
  }

  showDetail = (showWebDetail) => {
    this.setState({ showWebDetail: !showWebDetail });
  };

  stopPropagation = (e) => {
    e.stopPropagation();
  };

  previousPic = () => {
    let index = this.state.showingImgIndex;
    if (index === 0) {
      return;
    } else {
      this.setState({ showingImgIndex: --index });
    }
  };

  nextPic = () => {
    const { picturesNumber, showingImgIndex } = this.state;
    let index = showingImgIndex;
    if (showingImgIndex === picturesNumber - 1) {
      return;
    } else {
      this.setState({ showingImgIndex: ++index });
    }
  };

  renderDigitalDisplay = () => {
    const { showingImgIndex, picturesNumber } = this.state;
    const now = showingImgIndex + 1;
    const total = picturesNumber;
    return <div className="digital-display">{now + "/" + total}</div>;
  };

  renderIcon = () => {
    const { webImages } = this.props;

    if (webImages.length !== 1) {
      return (
        <React.Fragment>
          <i className="fa fa-arrow-left fa-3x" onClick={this.previousPic}></i>
          <i className="fa fa-arrow-right fa-3x" onClick={this.nextPic}></i>
        </React.Fragment>
      );
    }
  };

  renderImage = (url) => {
    const picture = require("./../../images/" + url);

    return (
      <li key={url} className="image-container">
        <img className="image-detail" src={picture} alt="" />
      </li>
    );
  };

  renderWebDetail = () => {
    const { webTitle, webImages, detailedIntroduction } = this.props;
    const { showWebDetail, showingImgIndex } = this.state;

    if (this.state.showWebDetail === false) {
      return;
    } else {
      return (
        <div className="web-detail-container" onClick={this.stopPropagation}>
          <div className="web-detail">
            <div className="detail-title">
              <h1>{webTitle}</h1>
              <i
                className="fa fa-times fa-3x"
                onClick={() => {
                  this.showDetail(showWebDetail);
                }}
              ></i>
            </div>
            <div className="images-container">
              {this.renderIcon()}
              {this.renderDigitalDisplay()}
              <ul className={"showing-index-" + showingImgIndex}>
                {webImages.map((url) => {
                  return this.renderImage(url);
                })}
              </ul>
            </div>
            <div className="detailed-introduction">
              <p>{detailedIntroduction}</p>
            </div>
          </div>
        </div>
      );
    }
  };

  render() {
    const { webTitle, coverUrl, webResume } = this.props;
    const { showWebDetail } = this.state;
    const cover = require("./../../images/webCover/" + coverUrl);

    return (
      <div
        className="web-block"
        onClick={() => {
          this.showDetail(showWebDetail);
        }}
      >
        {this.renderWebDetail()}
        <div className="web-resume">
          <h1>{webTitle}</h1>
          <p>{webResume}</p>
        </div>
        <div className="block-title">
          <h1>{webTitle}</h1>
        </div>
        <div className="block-content">
          <img src={cover} alt="webCover" />
        </div>
      </div>
    );
  }
}

export default WebBlock;
