import React, { Component } from "react";
import certificatePhoto from "../images/certificatePhoto.jpg";
import { Link } from "react-router-dom";
import "./navBar.scss";

class NavBar extends Component {
  state = {
    sizeChange: 0,
  };

  handleResize = () => {
    const { sizeChange } = this.state;
    const a = sizeChange + 1;
    this.setState({ sizeChange: a });
  };

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
  }

  renderNav() {
    if (document.body.clientWidth > 960) {
      return (
        <nav id="cp-nav">
          <div id="nav-block1">
            <Link to="/">
              <img
                id="certificatePhoto"
                src={certificatePhoto}
                alt="照片"
                width="160px"
                height="160px"
              />
            </Link>
            <h1 id="name">吴 飞 健</h1>
          </div>
          <div id="nav-block2">
            <ul id="ul-route">
              <Link to="./personalInfo">
                <li className="li-route">个人信息</li>
              </Link>
              <Link to="./websiteWorks">
                <li className="li-route">网站作品</li>
              </Link>
            </ul>
          </div>
          <div id="nav-block3">
            <div id="icon-contact">
              <Link to="/weChat">
                <i className="fa fa-weixin fa-2x"></i>
              </Link>
            </div>
          </div>
        </nav>
      );
    } else {
      return (
        <nav id="mobile-nav">
          <Link to="/personalInfo">
            <i className="fa fa-user fa-3x"></i>
          </Link>
          <Link to="/">
            <img src={certificatePhoto} alt="照片" height="100vmin" />
          </Link>
          <Link to="/websiteWorks">
            <i className="fa fa-code fa-3x"></i>
          </Link>
        </nav>
      );
    }
  }

  render() {
    return <React.Fragment>{this.renderNav()}</React.Fragment>;
  }
}

export default NavBar;
