import React, { Component } from "react";
import "./messageBlock.scss";
import PropTypes from "prop-types";

class MessageBlock extends Component {
  render() {
    const { mbTitle, mbContents } = this.props;

    return (
      <div className="message-block">
        <div className="block-title">
          <h1>{mbTitle}</h1>
        </div>
        <div className="block-content">
          {mbContents.map((mbContent) => (
            <div key={JSON.stringify(mbContent)}>
              <span className="content-title">{mbContent.contentTitle}</span>
              <span className="content-content">{mbContent.content}</span>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

MessageBlock.propTypes = {
  mbTitle: PropTypes.string.isRequired,
  mbContents: PropTypes.array.isRequired,
};

export default MessageBlock;
