import React, { Component } from "react";
import WebBlock from "./common/webBlock";
import { webBlocks } from "../datebase/webBlocks.json";
import "./websiteWorks.scss";

class WebsiteWorks extends Component {
  state = {
    webBlocks: [],
  };

  componentDidMount() {
    this.setState({ webBlocks });
  }

  render() {
    return (
      <React.Fragment>
        <div id="websiteWorks">
          <div id="wwTitle">
            <p>网站作品</p>
          </div>
          <div id="webBlocks">
            {webBlocks.map((webBlock) => (
              <WebBlock
                key={JSON.stringify(webBlock)}
                webTitle={webBlock.webTitle}
                coverUrl={webBlock.coverUrl}
                webResume={webBlock.webResume}
                webImages={webBlock.webImages}
                detailedIntroduction={webBlock.detailedIntroduction}
              ></WebBlock>
            ))}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default WebsiteWorks;
